<template>
  <CommomPop @close="closePop">
    <div class="wrap">
      <div class="name">{{ $t('admin.reviewReject') }}</div>
      <div class="input-wrap">
        <div class="name">{{ $t('admin.rejectReason') }}</div>
        <van-field class="input" input-align="center" v-model="reason" translate="no"/>
      </div>
      <div class="button-group">
        <div class="button-item" @click="handleConfirm">
          {{ $t('common.confirm') }}
        </div>
      </div>
    </div>
  </CommomPop>
</template>

<script>
import { handleAudit } from '@/services/task'
import CommomPop from '../../../components/common/CommomPop.vue'
export default {
  props: ['id'],
  components: { CommomPop },
  name: '',
  data () {
    return {
      reason: ''
    }
  },
  methods: {
    closePop () {
      this.$emit('close')
    },
    async handleConfirm () {
      const resp = await handleAudit({
        status: 2,
        reason: this.reason,
        id: this.id
      })
      if (!resp.success) return
      this.$parent.getList()
      this.closePop()
    }
  },
  mounted () {

  }
}
</script>

