<template>
  <div class="review-wrap">
    <template v-if="reviewList.length > 0">
      <ReviewItem
        v-for="item in reviewList"
        :key="item.id"
        :dataSource="item"
        @reject="handleReject"
      ></ReviewItem>
    </template>
    <div class="no-data" v-else>{{ $t('common.noData') }}</div>
    <RejectPop
      v-if="rejectShow"
      @close="handleReject"
      :id="rejectId"
    ></RejectPop>
  </div>
</template>

<script>
import { handleReviewList } from '@/services/task'
import RejectPop from './components/RejectPop.vue'
import ReviewItem from './components/ReviewItem.vue'
export default {
  name: '',
  data () {
    return {
      reviewList: [],
      rejectShow: false,
      rejectId: ''
    }
  },
  methods: {
    async getList () {
      const resp = await handleReviewList({
        taskId: this.$route.query.taskId
      })
      this.reviewList = resp.rows
    },

    handleReject (id) {
      this.rejectShow = !this.rejectShow
      this.rejectId = id
    }
  },
  mounted () {
    console.log('this.$route', this.$route)
    this.getList()
  },
  components: { ReviewItem, RejectPop }
}
</script>

<style scoped lang="scss">
.review-wrap {
  background-color: #ffffff;
  min-height: 100vh;
  padding: 20px 15px;
  color: #352c25;
}
</style>
