<template>
  <div class="review-item-wrap" @click="handleExpand">
    <div class="title">
      <div class="name">
        {{ $gbUtils.formatAddress(dataSource.address) || '***' }}
      </div>
      <div class="time">{{ dataSource.createTime || 0 }}</div>
    </div>
    <div class="content-no-expand" v-show="!isExpand">
      {{ dataSource.content }}
    </div>
    <div class="content-expand" v-show="isExpand">
      <div class="content">
        <img :src="dataSource.images" alt="" @click.stop="handleShowImg"/>
        <div class="text">
          {{ dataSource.content }}
        </div>
      </div>
      <div class="button-group" v-if="dataSource.status === 0">
        <div class="btn1" @click.stop="handleReject">
          {{ $t('admin.reject') }}
        </div>
        <div class="btn2" @click.stop="handlePass">{{ $t('admin.pass') }}</div>
      </div>
      <div class="button-reject" v-else-if="dataSource.status === 2">
        {{ $t('admin.rejected') }}
      </div>
      <div class="button-pass" v-else>{{ $t('admin.passed') }}</div>
    </div>
  </div>
</template>

<script>
import { handleAudit } from '@/services/task'
import { ImagePreview } from 'vant'
export default {
  props: ['dataSource'],
  name: '',
  data () {
    return {
      isExpand: true,
      instance_Preview: ''
    }
  },
  methods: {
    handleExpand () {
      this.isExpand = !this.isExpand
    },
    handleReject () {
      this.$emit('reject', this.dataSource.id)
    },
    async handlePass () {
      const resp = await handleAudit({
        status: 1,
        id: this.dataSource.id
      })
      if (!resp.success) return
      this.$parent.getList()
    },
    handleShowImg () {
      this.instance_Preview = ImagePreview([this.dataSource.images])
    }
  },
  mounted () {
    if (this.dataSource.status) {
      this.isExpand = false
    }
  },
  beforeDestroy () {
    if (this.instance_Preview) {
      this.instance_Preview = ''
    }
  }
}
</script>

