import request from '@/utils/request'

// 机构首页
export const handleAgencyInfo = (params) => {
  return request('GET', '/dapp/task/getTaskAdminIndex', params, true, true)
}

// 机构任务列表
export const handleAdminList = (params) => {
  return request('GET', '/dapp/task/adminList', params)
}

// 用户任务列表
export const handleList = (params) => {
  return request('GET', '/dapp/task/list', params, true, true)
}

// 用户任务列表
export const handleMyList = (params) => {
  return request('GET', '/dapp/taskLog/list', params, true, true)
}

// 任务详情
export const handleTaskDetail = (params) => {
  return request('GET', `/dapp/task/${params}`, true, true, 1)
}

// 新增任务
export const handleAddTask = (params) => {
  return request('POST', '/dapp/task', params)
}

// 修改任务
export const handleEditTask = (params) => {
  return request('PUT', '/dapp/task', params)
}

// 待审核列表
export const handleReviewList = (params) => {
  return request('GET', '/dapp/taskLog/pendingReview', params, true, true)
}

// 上传任务信息
export const handleUpload = (params) => {
  return request('POST', '/dapp/taskLog', params, true, true)
}

// 审核任务
export const handleAudit = (params) => {
  return request('POST', '/dapp/taskLog/audit', params, true, true)
}
